<template>
  <div>
    <app-gallery
            :images="page_gallery_lastest['images'][$route.params.gallery_category]"
            :button_back="page_gallery_lastest['buttons'][$route.params.lang]['back']">
    </app-gallery>
    <button class="pull-right" @click="navigateToFashion" v-if="$route.params.gallery_category == 'idar'">
      {{page_gallery_lastest['buttons'][$route.params.lang]['fashion']}}
    </button>
    <button class="pull-right" @click="navigateToIdar" v-if="$route.params.gallery_category == 'fashion'">
      {{page_gallery_lastest['buttons'][$route.params.lang]['idar']}}
    </button>
    <button class="btn btn-default"
            @click="$router.push('/'+$route.params.lang+'/'+$route.params.gallery_category+'/all')">
      {{page_gallery_lastest['buttons'][$route.params.lang]['all']}}
    </button>
  </div>
</template>

<script>
	import gallery from '../global/gallery.vue';
	export default {
		data(){
			return {
				page_gallery_lastest: {
					buttons: {
						ru: {
							fashion: 'Мода',
							idar: 'Дизайн интерьера',
							all: 'Все работы',
							back: 'Назад'
						},
						en: {
							fashion: 'Fashion',
							idar: 'Idar',
							all: 'All',
							back: 'Back'
						},
					},
					images: {
						idar: [
							'/static/img/Apartment_interior_design_imageNikolaev.png',
              '/static/img/TEST_IMAGES/1.jpg',
              '/static/img/gallery/latest/idar/1.jpg',
              '/static/img/gallery/latest/idar/2.jpg',
              '/static/img/gallery/latest/idar/3.jpg',
              '/static/img/gallery/latest/idar/4.jpg',
              '/static/img/gallery/latest/idar/5.jpg',
              '/static/img/gallery/latest/idar/6.jpg',
              '/static/img/gallery/latest/idar/7.jpg'],

						fashion: [
              '/static/img/gallery/latest/fashion/1.jpg',
              '/static/img/gallery/latest/fashion/2.jpg',
              '/static/img/gallery/latest/fashion/3.jpg',
              '/static/img/gallery/latest/fashion/4.jpg',
              '/static/img/gallery/latest/fashion/5.jpg',
              '/static/img/gallery/latest/fashion/6.jpg'],
					}
				},
			}
		},
		components: {
			appGallery: gallery
		},
		methods: {
			navigateToFashion(){
				this.$router.push('/' + this.$route.params.lang + '/gallery/lastest/fashion');
			},
			navigateToIdar(){
				this.$router.push('/' + this.$route.params.lang + '/gallery/lastest/idar/all');
			}
		},

	}
</script>


